const fruit = require('../static/fruit.json');
const projects = fruit.projects;
// projects.forEach(function (item) {
//   console.log(item.title);
// });
function createProjectList() {
  let html = '';
  projects.forEach(function (item) {
    html += `<li><a href="${item.url}" target="_blank">${item.title}</a></li>`;
  });
  return html;
}
